import React from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom";
//main
import Main from './routes/Main';
//sub
import AllGames from './routes/sub/AllGames'

import './App.css';




function App() {
  return (
      <>
          <div className="App">
            <BrowserRouter>
                <Routes>
                     <Route exact path='/' element={<Main/>} />
                     <Route
                         path="/game/all_games"
                         exact={true}
                         element={<AllGames/>}
                     />
                </Routes>
            </BrowserRouter>
         </div>
      </>
  );
}

export default App;
